* {
    box-sizing: border-box;
    margin: 0;
}

.HomePageBackgroundImgContainer {
  position: relative;
  display: flex;
  /* justify-content: center; */
  flex-direction: column;
  align-items: center;
  margin: auto;
  width: 100vw;
  height: 600px;
  /* background-image: url("/img/notebook.jpg"); */
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  overflow: hidden;
}

.HomePageBackgroundImgContainer::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.6); /* or black, depending on effect */
  z-index: 1;
}

.HomePageWelcomeText {
  position: relative;
  z-index: 2;
  width: 70%;
  color: black;
  color: #44745F;
  /* color: darkgray; */
  text-align: center;
  margin-top: 70px;
}

.homePageContactButton button {
    position: relative;
    z-index: 2;
    display: flex;
    justify-content: center;
    width: 70%;
    padding: 10px 20px;
    margin: 20px auto;
    font-size: 1.2rem;
    border-radius: 5px;
    border: none; 
    background-color: #44745F;
    color: #FAFAFA;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.homePageContactButton button:hover {
    transform: scale(1.05); /* Grows by 5% */
}


h1,
h2,
h3,
h4 {
    margin-bottom: 15px;
    font-weight: 400;
}

h1 {
    font-weight: 500;
}

h2 {
    font-size: 1.6rem;
}

h3 {
    font-size: 1.4rem;
}

h4 {
    font-size: 1.2rem;
}

.HomePageServices {
    background-color: #44745F;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 40px;
}

.HomePageServices button {
    width: 60%;
    padding: 10px 20px;
    font-size: 1.2rem;
    border: none;
    border-radius: 5px;
    color: #44745F;
    background-color: #FAFAFA;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.HomePageServices button:hover {
    transform: scale(1.05); /* Grows by 5% */
}

.HomePageServices p {
    font-size: 1.2rem;
}

.DyslexiaImgContainer img {
    width: 100%;
    object-fit: cover;
    object-position: center top;
}

.DyslexiaPrice {
    border-bottom: 1px solid #FAFAFA;
    margin-bottom: 10px;
}

.DyslexiaTitle, .LanguageTitle {
    color: #FAFAFA;
    font-weight: 200;
    margin: 10px 0;
}

p {
    color: #FAFAFA;
    font-weight: 200;
}

.m10px {
    margin-bottom: 10px;
}

.m30px {
    margin-bottom: 30px;
}
.m60px {
    margin-bottom: 60px;
}

.flex {
    display: flex;
    flex-direction: column;
}

@media (min-width: 700px) {
    #Dyslexia, #Language {
        display: flex;
        justify-content: space-between;
        margin-bottom: 100px;
        max-width: 1000px;
    }

    #Language {
        flex-direction: row-reverse;
    }

    #Dyslexia button, #Language button {
        width: 100%;
    }

    #Dyslexia img, #Language img {
        max-width: 500px;
    }

    .homePageContactButton button {
        width: 80%;
    }
  /* Your desktop-specific styles here */
}


